$label-color: rgba(28, 1, 255, 1);
@mixin clearfix() {
  &::after {
    display: block;
    content: "";
    clear: both;
  }
}

.loader{
    display: flex;
    justify-content: center;
    align-items: center;
    min-height: 500px;
    .icon{
        width: 100px;
        height: 100px;
        display: block;
        background-image: url(../icons/loading.svg);
        background-repeat: no-repeat;
        background-size: cover;
    }
}